@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Monsterrat', sans-serif;
}

body {
  margin: 0;
}


.input-label { 
  letter-spacing: 0.1em;
  font-size: 17px;
  color: #6a4d3e; 
  margin: 1em 0em 0.5em 0em;
  display: inline-block;

}

.login { 
background-color: #fef0f0; 
border: 6px solid forestgreen;
border-radius: 30px;
width: 40%; 
margin: 0 auto;
text-align: center;
color: #108150; 
 overflow: hidden; 
 flex-direction: column;
 display: flex; 
 justify-content: center;
 justify-self: center;
 font-family: 'Montserrat'; 
}

button { 
  cursor: pointer; 
  width: 40%; 
  text-align: center;
  display: inline-block;
  margin: 0 auto;
  padding: 0.5em 0em 0.5em 0em;
  font-weight: bold;
  color: white; 
  border-radius: 30px;
  border: 0px; 
  font-size: 20px;
  font-family: 'Major Mono Display';
  background-color: green; 
}


button:hover { 
  transition: 0.25s ease; 
  background-color: green;
  color: white; 
}

input { 
  display: inline-block; 
  width: 60%; 
  height: 100%;
  margin: 0 auto;
}

.hover { 
  cursor: pointer;
  font-weight: bold;
}
.hover:hover { 
  transition: ease 0.1s;
  color: #6a4d3e;
}

.error { 
  color: red;
  opacity: 0.25; 
}


.not-auth { 
  text-align: center;
  border: 3px solid #6a4d3e; 
  border-radius: 10px;
}

.login-reg { 
  margin-top: 0;
}

.nav { 
  width: 100%;
  height: 60px;
  background-color: #6a4d3e;
  overflow: hidden;
  display: flex;
  margin: 0 auto; 
  padding: 10px 0px 10px;
  grid-column-gap: 0em;
  -webkit-column-gap: 0em;
          column-gap: 0em;
  background-image: url("https://64.media.tumblr.com/9d1002c2ac7c18e4c9dc6de85cc75c79/tumblr_n4i3p4CmSP1snc5kxo1_500.gif");
  background-size: contain;
  justify-items: center;
}
.navbutton { 
  width: 25%;
  background-color: green;
  color: white;
  border: 0px;
  margin: 0 auto; 
}
button:hover { 
  transition: 0.25s ease;
  color: black;
  background-color: white;
}
/* Popup style */
.overlay {
  background: #00000050;
  width: 100%;
  position: fixed; 
  top: 0; 
  bottom: 0; 
  right: 0; 
  left: 0; 
  height: 100vh;
  margin: 0 auto; 
  
}


.header{
  position: relative;
  letter-spacing: 0.5em;
  text-align: center;
  font-family: 'Major Mono Display'; 
  font-size: 27px; 
}

 
.box {
  object-position: contain;
  position: relative;
  width: 40%;
  margin: 0 auto;
  height: auto;
  max-height: 75vh;
  margin-top: calc(100vh - 85vh - 20px);
  background-color: #fadccf; 
  border-radius: 10px;
  padding: 20px;
  border: 1px solid #999;
  overflow: show;
  display: flex; 
  justify-content: center;
  font-family: 'Montserrat';
  letter-spacing: 0.10em;
  font-size: 15px;
}
 
 .close {
  content: 'X';
  cursor: pointer;
  color: grey; 
  position: relative; 
  top: 0; 
  left: 0; 
  width: 25px;
  height: 25px;
  text-align: center;
  font-size: 30px;
  text-decoration: none;
}

.close:hover {
  transition: 0.1s ease; 
  color: white;
}

input:focus { 
  outline: none;
}


button { 
  display: flex; 
  margin-top: 1em; 
  justify-content: center;
}


.content { 
  width: 100%;
  text-align: center;
}


.fields { 
  height: 2.5em; 
  width: 13em; 
  border: 2px solid green; 
  padding: 10px; 
  border-radius: 2em;
  margin: 4px; 
}

#frequency {
  width: 4em; 
  text-align: center;
}

#notes { 
  overflow-x: hidden;
  overflow-y: scroll;
  width: 30em; 
  outline: none;
  padding-left: 1.5em;
  font-family: 'Arial'; 
  line-height: 15px;
  height: 5em; 
}

#deleted { 
  color: white;   
  display: inline;
  margin: 0 auto; 
  padding: 0; 
  height: 1em; 
  width: auto; 
  border: 4px solid green; 
  border-radius: 100%;

}

#edit-notes { 
  width: 30em; 
  height: 5em; 
  outline: none;
  padding-left: 1.5em;
  font-family: 'Arial';

}

#ext { 
  overflow: show;
  height: 50em; 
}

#save {
  margin-top: 0 auto;
  margin-bottom: 10em;
}
#list { 
  display: grid; 
  width: 100%; 
  grid-template-columns: 40% 40%;
  margin: 0 auto; 
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.card { 
  border: 5px solid #6a4d3e; 
  overflow: hidden;
  text-align: center;
  background-color: #faf2ed;
  padding: 4px; 
  border-radius: 14px;
  font-family: 'Montserrat'; 
}

.edit { 
  width: 50%; 
  background-color: green; 
  display: flex;
  justify-content: center;
  color: white; 
  font-size: 15px;
}


.photo { 
  overflow: hidden; 
  object-position: center;
  object-fit: cover;
  margin-top: 1em;
  max-width: 400px;
  max-height: 400px;
  border-radius: 5px; 
}


.alert { 
  text-align: center; 
  font-family: 'Montserrat'; 
  color: green; 
}

.new-plant {
  font-size: 50px; 
  font-family: 'Major Mono Display'; 
  text-align: center; 
  color: #6a4d3e; 
}





.main { 
  color: #108150; 
  padding: 0.5em 0em 0.25em 0em; 
  font-family: 'Major Mono Display';
  margin: 0 auto;
  text-align: center;
  font-size: 5em; 
}

